import React, { useState } from 'react';
import axios from 'axios';

function Connection() {
  const [input1, setInput1] = useState('');
  const [input2, setInput2] = useState('');
  const [qr1, setQr1] = useState(null);
  const [qr2, setQr2] = useState(null);
  const [connectionStatus, setConnectionStatus] = useState(null);

  const handleSearch = async () => {
    // 清空之前的结果
    setQr1(null);
    setQr2(null);
    setConnectionStatus(null);

    try {
      // 获取第一个地址的QR图片
      const qrResponse1 = await axios.get(`https://api.ethfollow.xyz/api/v1/users/${input1}/qr`, {
        headers: { 'Accept': 'image/svg+xml' }
      });
      setQr1(qrResponse1.data);

      // 获取第二个地址的QR图片
      const qrResponse2 = await axios.get(`https://api.ethfollow.xyz/api/v1/users/${input2}/qr`, {
        headers: { 'Accept': 'image/svg+xml' }
      });
      setQr2(qrResponse2.data);

      // 检查关注关系
      let response = await axios.get(`https://api.ethfollow.xyz/api/v1/users/${input1}/searchFollowers?term=${input2}`);
      if (response.data.followers.length === 0) {
        response = await axios.get(`https://api.ethfollow.xyz/api/v1/users/${input2}/searchFollowers?term=${input1}`);
        if (response.data.followers.length === 0) {
          setConnectionStatus('none');
        } else {
          setConnectionStatus('single');
        }
      } else {
        setConnectionStatus('single-reverse');
      }

      if (response.data.followers.length > 0 && response.data.followers[0].is_following) {
        setConnectionStatus('double');
      }
    } catch (error) {
      console.error('Error fetching data:', error);
    }
  };

  return (
    <div class="show">
      <input
        type="text"
        value={input1}
        onChange={(e) => setInput1(e.target.value)}
        placeholder="Enter first ENS or address"
        className="input"
      />
      <input
        type="text"
        value={input2}
        onChange={(e) => setInput2(e.target.value)}
        placeholder="Enter second ENS or address"
        className="input"
      />
      <button onClick={handleSearch}>Check</button>
      <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', marginTop: '20px' }}>
        {qr1 && <div dangerouslySetInnerHTML={{ __html: qr1 }} />}
        {connectionStatus && (
          <div style={{ margin: '0 20px', fontSize: '2em', color: 'green' }}>
            {connectionStatus === 'double' && <span>↔️</span>}
            {connectionStatus === 'single' && <span>→</span>}
            {connectionStatus === 'single-reverse' && <span>←</span>}
            {connectionStatus === 'none' && <span style={{ color: 'black' }}>✖️</span>}
          </div>
        )}
        {qr2 && <div dangerouslySetInnerHTML={{ __html: qr2 }} />}
      </div>
    </div>
  );
}

export default Connection; 