import React from 'react';
import { BrowserRouter as Router, Route, Routes, NavLink } from 'react-router-dom';
import EFPDash from './components/EFPDash';
import ListsChecker from './components/ListsChecker';
import Connection from './components/Connection';
import POAPs from './components/POAPs';

function App() {
  return (
    <Router>
      <div className="App">
        <img src={`${process.env.PUBLIC_URL}/logo.png`} alt="Logo" className="logo" />
        <h1 style={{ textAlign: 'center' }}>EFPFinder</h1>
        <nav>
          <ul style={{ display: 'flex', justifyContent: 'center', listStyle: 'none', padding: 0 }}>
            <li style={{ margin: '0 10px' }}>
              <NavLink to="/efpdash" className={({ isActive }) => isActive ? 'active-link' : ''}>EFPDash</NavLink>
            </li>
            <li style={{ margin: '0 10px' }}>
              <NavLink to="/listschecker" className={({ isActive }) => isActive ? 'active-link' : ''}>ListsChecker</NavLink>
            </li>
            <li style={{ margin: '0 10px' }}>
              <NavLink to="/connection" className={({ isActive }) => isActive ? 'active-link' : ''}>Connection</NavLink>
            </li>
            <li style={{ margin: '0 10px' }}>
              <NavLink to="/poaps" className={({ isActive }) => isActive ? 'active-link' : ''}>POAPs</NavLink>
            </li>
          </ul>
        </nav>
        <Routes>
          <Route path="/efpdash" element={<EFPDash />} />
          <Route path="/listschecker" element={<ListsChecker />} />
          <Route path="/connection" element={<Connection />} />
          <Route path="/poaps" element={<POAPs />} />
          <Route path="/" element={<EFPDash />} />
        </Routes>
        <footer style={{ position: 'fixed', bottom: 0, width: '100%', textAlign: 'right', marginTop: '20px', marginRight: '20px', whiteSpace: 'nowrap' }}>
          <p>© 2025 EFPFinder. Backed by <a href={`https://x.com/realjaxo`} target="_blank" rel="noopener noreferrer">jaxo</a></p>
        </footer>
      </div>
    </Router>
  );
}

export default App; 