import React, { useState } from 'react';
import axios from 'axios';

function EFPDash() {
  const [input, setInput] = useState('');
  const [data, setData] = useState(null);

  const handleSearch = async () => {
    // 清除显示区域
    setData(null);

    try {
      const detailsResponse = await axios.get(`https://api.ethfollow.xyz/api/v1/users/${input}/details`);
      const statsResponse = await axios.get(`https://api.ethfollow.xyz/api/v1/users/${input}/stats`);
      const qrResponse = await axios.get(`https://api.ethfollow.xyz/api/v1/users/${input}/qr`, {
        headers: { 'Accept': 'image/svg+xml' }
      });

      setData({
        details: detailsResponse.data,
        stats: statsResponse.data,
        qr: qrResponse.data,
      });
    } catch (error) {
      console.error('Error fetching data:', error);
      alert('Failed to fetch data. Please check the ENS or address and try again.');
    }
  };

  return (
    <div class="show">
      <input
        type="text"
        value={input}
        onChange={(e) => setInput(e.target.value)}
        placeholder="Enter ENS or address"
        className="input"
      />
      <button onClick={handleSearch}>Check</button>
      {data && (
        <div className="card" style={{ marginTop: '20px', display: 'flex', flexDirection: 'row' }}>
          <div className="card-content" style={{ textAlign: 'center', flex: 1 }}>
            <img
              src={data.details.ens.avatar}
              alt="Avatar"
              style={{ width: '80px', height: '80px', borderRadius: '50%', margin: '0 auto' }}
            />
            <h2 style={{ margin: '10px 0' }}>{data.details.ens.name}</h2>
            <div className="data-cards">
              <div className="data-card">
                <span>Followers</span>
                <span>{data.stats.followers_count}</span>
              </div>
              <div className="data-card">
                <span>Following</span>
                <span>{data.stats.following_count}</span>
              </div>
              <div className="data-card">
                <span>Mutuals</span>
                <span>{data.details.ranks.mutuals_rank}</span>
              </div>
              <div className="data-card">
                <span>Followers</span>
                <span>{data.details.ranks.followers_rank}</span>
              </div>
              <div className="data-card">
                <span>Following</span>
                <span>{data.details.ranks.following_rank}</span>
              </div>
              <div className="data-card">
                <span>Top8</span>
                <span>{data.details.ranks.top8_rank}</span>
              </div>
              <div className="data-card">
                <span>Blocks</span>
                <span>{data.details.ranks.blocks_rank}</span>
              </div>
            </div>
          </div>
          <div className="card-content" style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', flex: 1 }}>
            <div dangerouslySetInnerHTML={{ __html: data.qr }} style={{ width: '70%', height: '70%' }} />
          </div>
        </div>
      )}
    </div>
  );
}

export default EFPDash; 