import React, { useState } from 'react';
import axios from 'axios';

function POAPs() {
  const [input, setInput] = useState('');
  const [poaps, setPoaps] = useState([]);

  const handleSearch = async () => {
    // 清除显示区域
    setPoaps([]);

    try {
      const response = await axios.get(`https://api.ethfollow.xyz/api/v1/users/${input}/badges`);
      const ownedPoaps = response.data.poaps.filter(poap => poap.participated && poap.collection);
      setPoaps(ownedPoaps);
    } catch (error) {
      console.error('Error fetching data:', error);
    }
  };

  return (
    <div class="show">
      <input
        type="text"
        value={input}
        onChange={(e) => setInput(e.target.value)}
        placeholder="Enter ENS or address"
        className="input"
      />
      <button onClick={handleSearch}>Check</button>
      <div className="poap-cards" style={{ marginTop: '20px' }}>
        {poaps.map((poap, index) => (
          <a key={index} href={`https://collectors.poap.xyz/token/${poap.collection.tokenId}`} target="_blank" rel="noopener noreferrer" className="poap-card">
            <img
              src={poap.collection.event.image_url}
              alt={poap.collection.event.name}
              style={{ width: '200px', height: '200px', borderRadius: '50%', margin: '0 auto' }}
            />
            <p style={{ textAlign: 'center', marginTop: '10px' }}>{poap.collection.tokenId}</p>
          </a>
        ))}
      </div>
    </div>
  );
}

export default POAPs; 