import React, { useState } from 'react';
import axios from 'axios';

function ListsChecker() {
  const [input, setInput] = useState('');
  const [data, setData] = useState(null);

  const handleSearch = async () => {
    // 清除显示区域
    setData(null);

    try {
      const response = await axios.get(`https://api.ethfollow.xyz/api/v1/users/${input}/lists`);
      setData(response.data);
	  
	  let response2 = await axios.get(`https://api.ethfollow.xyz/api/v1/users/${input}/details`);
	  if (response2.data.ens.name === 0) {
		setUser(response2.data.address);
	  } else {
		setUser(response2.data.ens.name);
	  }
    } catch (error) {
      console.error('Error fetching data:', error);
    }
  };

  return (
    <div class="show">
      <input
        type="text"
        value={input}
        onChange={(e) => setInput(e.target.value)}
        placeholder="Enter ENS or address"
        className="input"
      />
      <button onClick={handleSearch}>Check</button>
      {data && (
        <div className="card" style={{ marginTop: '20px' }}>
          <div className="card-content">
            <h2>{User}</h2>
            <div className="highlight">
              <p>Number of Lists: <strong>{data.lists.length}</strong></p>
              <p>Primary List: <strong>{data.primary_list}</strong></p>
            </div>
            <div className="list-numbers">
              {data.lists.map((list, index) => (
                <a key={index} href={`https://efp.app/${list}`} target="_blank" rel="noopener noreferrer" className="list-number">
                  {list}
                </a>
              ))}
            </div>
          </div>
        </div>
      )}
    </div>
  );
}

export default ListsChecker; 